	module.exports = {"data":{"site":{"siteMetadata":{"title":"Eh, I Could Eat","author":"Jacob Aronoff"}},"markdownRemark":{"id":"/Users/jea/Documents/Code/github/icouldeat/src/pages/mud-spot/index.md absPath of file >>> MarkdownRemark","html":"<h3>Avocado Sandwich</h3>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/picture-eeb8c4f3eff34ec0e464a2e5916ceb94-01800.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 75%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Avocado Sandwich\"\n        title=\"\"\n        src=\"/static/picture-eeb8c4f3eff34ec0e464a2e5916ceb94-f8fb9.jpg\"\n        srcset=\"/static/picture-eeb8c4f3eff34ec0e464a2e5916ceb94-e8976.jpg 148w,\n/static/picture-eeb8c4f3eff34ec0e464a2e5916ceb94-63df2.jpg 295w,\n/static/picture-eeb8c4f3eff34ec0e464a2e5916ceb94-f8fb9.jpg 590w,\n/static/picture-eeb8c4f3eff34ec0e464a2e5916ceb94-85e3d.jpg 885w,\n/static/picture-eeb8c4f3eff34ec0e464a2e5916ceb94-d1924.jpg 1180w,\n/static/picture-eeb8c4f3eff34ec0e464a2e5916ceb94-9452e.jpg 1770w,\n/static/picture-eeb8c4f3eff34ec0e464a2e5916ceb94-01800.jpg 4032w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>For the price, this place is totally worth it. $19 for any dish on their menu, beer/mimosa, and Oj/ coffee. I got the avocado sandwich, because I was trying to be a bit healthier with my choices, but it was still really good. I think overall it would have been made a bit better by a more flavorful addition to offset the mildness / texture of the avocado. There were a bunch of over things on the menu, though, that sounded amazing. I’m excited to try it again.</p>\n<p>Rating: 7.5/10, probably going to be higher with a second visit.</p>","frontmatter":{"title":"Mud Spot","date":"January 13, 2018"}}},"pathContext":{"slug":"/mud-spot/","previous":{"fields":{"slug":"/raclette/"},"frontmatter":{"title":"Raclette"}},"next":{"fields":{"slug":"/terra/"},"frontmatter":{"title":"Terra"}}}}

